@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body{margin:0}
.Entete{width:100%;background-color:#333;color:#fff;display:flex;flex-direction:row;align-items:center;padding:5px 0}.Entete .logo{margin-left:10px}.Entete .avatar{display:flex;align-items:center;margin-left:auto;margin-right:15px}.Entete .avatar :first-child{margin-left:5px}
.Dossier{display:flex;flex-direction:column;position:relative}.Dossier .couverture{position:relative;height:200px}.Dossier .couverture .deplacer{position:absolute;display:flex;align-items:center;justify-content:center;top:10px;left:10px;width:40px;height:40px;opacity:.5;background-color:#000;-webkit-clip-path:circle(50%);clip-path:circle(50%);cursor:pointer}.Dossier .couverture .deplacer :first-child{color:#fff}.Dossier .couverture img{height:100%}.Dossier .info{color:#fff;padding-left:15px;padding-bottom:40px}.Dossier .plus{position:absolute;bottom:15px;right:10px;color:#fff;cursor:pointer}
.ListeDossiers{display:flex;flex-flow:row wrap;justify-content:center;list-style-type:none;margin-top:40px;margin-left:-20px;-webkit-padding-start:0;padding-inline-start:0}.ListeDossiers li{margin-left:20px;margin-bottom:25px}

body{font-family:"Roboto",sans-serif}body .Appli button{position:fixed;bottom:30px;right:30px}
